:root {
  --black: #150605;
  --red: #EA453C;
  --pink: #F47B6E;
  --light-blue: #EFFBFE;
  --blue: #ACE6EC;
}

* {
  margin: 0;
  padding: 0;
}

html, body {
  font-family: 'Oswald', sans-serif;
}

header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: var(--pink);
  color: var(--light-blue);
  padding: 10px;
  box-shadow: 0px 2px 10px -5px rgba(21,6,5,1);
}

a {
  text-decoration: none;
}

a:visited {
  color: var(--light-blue);
}

a:hover {
  color: var(--light-blue);
}

a:active {
  color: var(--light-blue);
}

.photos {
  display: grid;
  padding: 10px;
  grid-gap: 2px;
  grid-auto-rows: 100px;
  grid-template-columns: repeat(auto-fit, minmax(100px, 1fr));
  grid-auto-flow: dense;
}

.image-grid {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.image-container {
  position: relative;
}

.wide {
  grid-column: span 2;
}

.tall {
  grid-row: span 2;
}

.big {
  grid-column: span 2;
  grid-row: span 2;
}

.favorite {
  color: var(--red);
  font-size: 1.2rem;
  position: absolute;
  top: 5px;
  left: 5px;
  cursor: pointer;
}

.cart {
  color: var(--blue);
  font-size: 1.2rem;
  position: absolute;
  top: 5px;
  right: 5px;
  cursor: pointer;
}

.ri-heart-fill {
  opacity: .7;
}

.ri-delete-bin-fill, .ri-delete-bin-line {
  font-size: 1.3em;
  cursor: pointer;
}

.cart-page > h1 {
  text-align: center;
}

.cart-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 20px 0;
}

.cart-item img {
  margin-right: auto;
  margin-left: 10px;
}

.cart-page .total-cost {
  text-align: right;
  font-size: 1.3em;
}

.cart-page .order-button {
  display: flex;
  justify-content: center;
}

.order-button button {
  border-color: var(--black);
  cursor: pointer;
  padding: 15px;
  transition: all .2s ease-in-out;
}

.order-button button:focus {
  outline: none;
}

.order-button button:disabled {
  border-color: lightgray;
}